import BaseApiService from "../BaseApiService";

const DEFAULT_PATH = "/custom/api/point/interest";

class PointOfInterestService extends BaseApiService {
  getAll = (routeParams: string) => {
    const url = `${DEFAULT_PATH}/key/metrics/${routeParams}`;
    return this.makeGetRequestWithAuth(url);
  };
}

export default new PointOfInterestService();
